//Library
import React, { useEffect } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
//Component
import Header from "../../components/Mobile/Header/Header";
import ShippingContainer from "./ShippingContainer";
// Helper Function
import { readCookies } from "../../utils/readBrowserCookies";
import { verifyToken } from "../../commonAjaxCall/createAnonymousToken";

const Shipping = () => {
  const headerConfig = [
    {
      content: <ion-icon name="arrow-back-outline"></ion-icon>,
      action: () => window.history.back(),
    },
    {
      content: (
        <div className="shippingStep">
          <div>Step 2 of 3</div>
          <div>Delivery Details</div>
        </div>
      ),
      action: "",
    },
  ];
  const token = readCookies("A");
  // if token existed in client then validate it is login or not in backend
  useEffect(() => {
    token &&
      readCookies("user_type") === "L" &&
      verifyToken({
        path: "signin?referrer=shipping",
        redirect: true,
      });
  }, []);
  //  if token not existed  in client  then redirect  user to login
  if (!token) {
    return <Redirect to="/signin?referrer=shipping" />;
  }
  return (
    <div className="shippingPage">
      <Header headerConfig={headerConfig} classProp="shippingHeader" />
      <ShippingContainer />
    </div>
  );
};

function mapStateToProps() {}
function mapDispatchToProps(dispatch) {
  return {
    // getShippingAddress: () => dispatch(shippingAddress())
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(Shipping);
