export const SHIPPING_ACTION = {
  shippingAddress: "SHIPPING_ADDRESS",
  productList: "PRODUCT_LIST",
  addAddress: "ADD_ADDRESS",
  addressList: "ADDRESS_LIST",
  editAddress: "EDIT_ADDRESS",
  pinCode: "PINCODE_VALIDATION",
};

export const defaultAddress = (payload = {}) => ({
  type: SHIPPING_ACTION.shippingAddress,
  payload,
});
export const shippingProducts = (payload = {}) => ({
  type: SHIPPING_ACTION.productList,
  payload,
});
export const addNewAddress = (payload = {}) => ({
  type: SHIPPING_ACTION.addAddress,
  payload,
});
export const addressList = (payload = {}) => ({
  type: SHIPPING_ACTION.addAddress,
  payload,
});
export const editAddress = (payload = {}) => ({
  type: SHIPPING_ACTION.editAddress,
  payload,
});
export const pincodeAddress = (payload = {}) => ({
    type: SHIPPING_ACTION.pinCode,
    payload,
  });