import { SHIPPING_ACTION } from "./shipping-action";
export const shippingReducer = (state = {}, action = {}) => {
  const { type = "", payload = "" } = action;
  switch (type) {
    case SHIPPING_ACTION.shippingAddress:
      return {
        shippingAddress:payload,
        ...state,
      };
    case SHIPPING_ACTION.productList:
      return {
        productList:payload,
        ...state,
      };
    case SHIPPING_ACTION.addAddress:
      return {
        addAddress:payload,
        ...state,
      };
    case SHIPPING_ACTION.addressList:
      return {
        addressList:payload,
        ...state,
      };
    case SHIPPING_ACTION.editAddress:
      return {
        editAddress:payload,
        ...state,
      };
    case SHIPPING_ACTION.pinCode:
      return {
        addressBasedOnPinCode:payload,
        ...state,
      };
    default:
      return state;
  }
};
